import { ChangeDetectorRef, Component, OnChanges } from '@angular/core';
import { ReactiveFormsModule } from '@angular/forms';
import { FormGroup, FormControl } from '@angular/forms';
import { ButtonModule } from 'primeng/button';
import { InputTextModule } from 'primeng/inputtext';
import { CalendarModule } from 'primeng/calendar';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { Transaction } from './models/transaction';
import { DropdownModule } from 'primeng/dropdown';
import { HttpService } from 'src/app/services/http.service';
import { TableModule } from 'primeng/table';
import { CommonModule } from '@angular/common';



@Component({
  selector: 'app-transactions',
  standalone: true,
  imports: [ReactiveFormsModule, ButtonModule, InputTextModule, CalendarModule, DropdownModule, TableModule, CommonModule],
  providers: [HttpService],
  templateUrl: './transactions.component.html',
  styleUrl: './transactions.component.css'
})
export class TransactionsComponent {

  transaction: any;
  transaction_type = ['income', 'expense']

  url = 'http://127.0.0.1:8000/api/transaction/'
  constructor(private httpService: HttpService, private cdr: ChangeDetectorRef) {}
  cols = ['id', 'amount', 'transactionDate', 'transactionType', 'updatedAt', 'note', 'category', 'Action']
  products: any;

  ngOnInit() {
    this.transaction = new FormGroup({
      amount: new FormControl<number>(0),
      transaction_date: new FormControl<Date | null>(null),
      transaction_type: new FormControl<'income' | 'expense'>('expense'),
      note: new FormControl(),
      category: new FormControl()
    })
    this.getTransactionData();
  }

  getTransactionData() {
    this.httpService.get(this.url).subscribe(
      response => this.products = response
    )
  }

  onSubmit() {
    console.log(this.transaction.value, 'this.transaction')
  }

  handleDelete(id: any) {
    this.httpService.delete(`${this.url}${id}/`).subscribe({
      complete: () => {
        this.getTransactionData();
      }
    })
  }
  
}
